@use "global" as *;


.fv {
  padding-top: rem(116);  // headerの高さ分を考慮 (border分も)
  min-height: rem(900);

  @include mq(md) {
    padding-top: rem(77);  // headerの高さ分を考慮 (border分も)
    min-height: auto;
  }
}

.fv__inner {
  position: relative;
}

.fv__video {
  display: block;
  width: 100%;
  object-fit: cover;
  min-height: rem(810);
  max-height: rem(810);

  aspect-ratio: 1440 / 810;
  object-fit: cover;

  @include mq(md) {
    display: none;
  }
}

.fv__video-sp {  // SP時の動画
  display: none;

  @include mq(md) {
    display: block;
    width: 100%;
    object-fit: cover;
    max-height: rem(810);

    aspect-ratio: 1440 / 810;
    object-fit: cover;
    min-height: auto;
  }
}

.fv__video-alternative-text {  // 動画が表示されない場合の代替テキスト (念の為に設定)
  font-size: rem(20);
  text-align: center;
  color: $color-font-base-1;

  @include mq(md) {
    font-size: rem(18);
  }
}

.fv__text {  // 非表示。
  display: none;  // (文字は、動画内に埋め込む方法に仕様変更のため、文字は非表示に。)

